/* ------------------------------------------ */
.mapMarkerWrapNewDesign {
  width: 100px;
  height: 100px;
  background-size: cover;
  background-position: center;
  background-color: #000;
  border-radius: 10px;
  position: relative;
  bottom: 110px;
  right: 50px;
  -webkit-transition: 0.3s ease opacity;
  transition: 0.3s ease opacity;
}
.mapMarkerWrapNewDesign:hover {
  opacity: 0.9;
}
.mapMarkerGradientWrapperNewDesign {
  overflow: hidden;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#000000),
    color-stop(52.08%, rgba(196, 196, 196, 0)),
    color-stop(90.62%, rgba(16, 16, 16, 0.92))
  );
  background: linear-gradient(
    180deg,
    #000000 0%,
    rgba(196, 196, 196, 0) 52.08%,
    rgba(16, 16, 16, 0.92) 90.62%
  );
  display: flex;
  flex: 1 1;
  align-items: flex-end;
}
.mapMarkerDescNewDesign {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 32px;
  padding: 3px;
  padding-right: 6px;
}
.mapMarkerNameNewDesign {
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 16px;
}
.isOpenedMapNewDesign {
  color: #36cc33;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 7px;
  line-height: 9px;
}
.workTimeMapNewDesign {
  font-weight: 600;
  font-size: 8px;
  line-height: 9px;
  color: #ffffff;
}
.typeOfCompanyMapNewDesign {
  color: #ff0960;
  font-size: 9px;
  font-weight: 900;
  text-transform: uppercase;
  position: absolute;
  top: 6px;
  right: 6px;
}

.bottomMapMarkerTextWrapNewDesign {
  display: flex;
  justify-content: space-between;
  line-height: 16px;
  padding: 2px;
}
.mapNoTranslationTextNewDesign {
  color: #919191;
  text-align: center;
  padding: 3px;
}
.companyImgMapNewDesign {
  object-fit: cover;
  -webkit-transition: 0.2s ease all;
  transition: 0.2s ease all;
  height: 100%;
  display: flex;
  /* padding-top: 15px; */
  padding-bottom: 26px;
  justify-content: center;
  align-items: center;
}
.arrowMapNewDesign {
  width: 0;
  height: 0;
  margin: 0 auto;
  border: 10px solid transparent;
  border-top-color: #000;
  border-bottom: 0;
  position: absolute;
  bottom: 0px;
  left: -10px;
}
