.homeContentWrap {
  padding-top: 50px;
  width: 1000px;
  margin: 0 auto;
}

.navContainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  height: 54px;
}
.noOneCompany {
  width: 100%;
  text-align: center;
  font-size: 22px;
  padding: 30px;
}
.homeContent {
  margin-top: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
}
@media screen and (max-width: 760px) {
  body {
    background-color: #eef1f6;
  }
  .homeContentWrap {
    position: relative;
    padding-top: 100px;
    width: 100%;
    padding-bottom: 65px;
  }
  .homeContent {
    -webkit-transition: 0.3s ease all 0.2s;
    -o-transition: 0.3s ease all 0.2s;
    transition: 0.3s ease all 0.2s;
    margin-top: 10px;
    margin-right: 5px;
    margin-left: 5px;
  }
  .navContainer {
    height: 0;
    margin: 0;
  }
}
